.cart__container {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.639);
	z-index: 99999;
 }
 
 .cart {
	position: absolute;
	top: 0;
	right: 0;
	width: 400px;
	height: 100%;
	background: #fff;
	z-index: 999999;
 }
 
 .cart__close {
	width: 100%;
	height: 60px;
 
	padding: 10px 20px;
 }
 
 .cart__close span i {
	font-size: 1.1rem;
	background: #212245;
	padding: 5px;
	border-radius: 50%;
	color: #fff;
	cursor: pointer;
 }
 .cart__item-list {
	height: calc(100vh - 140px);
 }
 
 .cart__bottom {
	position: absolute;
	bottom: 0;
	left: 0;
	padding: 5px 20px;
	width: 100%;
	height: 80px;
	background: #df2020;
 }
 
 .cart__bottom h6 {
	font-size: 1rem;
	color: #fff;
 }
 
 .cart__bottom h6 span {
	font-size: 1.3rem;
	font-weight: 600;
 }
 
 .cart__bottom button {
	border: none;
	padding: 4px 20px;
	border-radius: 5px;
	background: #fff;
	color: #212245;
 }
 
 .cart__bottom button a {
	color: #212245;
	font-weight: 600;
 }
 
 @media only screen and (max-width: 576px) {
	.cart {
	  width: 300px !important;
	}
 }
/* google fonts */

/* reset default style */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* base style */

body {
  background: #fcfcfc;
  font-family: "Istok Web", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "RocknRoll One", sans-serif !important;
}

a {
  text-decoration: none;
  color: unset;
}

h1,
h2 {
  font-size: 2.1rem;
  font-weight: 600;
  color: #212245;
}

section {
  padding: 60px 0px;
}

@media only screen and (max-width: 768px) {
  section {
    padding: 40px 0px;
  }

  h1,
  h2 {
    font-size: 1.5rem;
  }
}
